import React from 'react';
import {
  StyledSection,
  TextContainer,
  SectionTitle,
  SectionDescription,
  SectionNumber,
} from './information-section.styles';
import { SectionImage } from '../../about/section/section.styles';

interface InformationSectionProps {
  title: string;
  description: string;
  image: 'workspace' | 'content';
}

export const InformationSection = ({
  title,
  description,
  image,
}: InformationSectionProps) => {
  const containerRef = React.createRef<HTMLDivElement>();
  const [containerHeight, setContainerHeight] = React.useState(0);
  const [isActive, setActive] = React.useState(false);

  const handleResize = React.useCallback(() => {
    setContainerHeight(containerRef.current?.offsetHeight);
  }, [containerRef]);

  const handleScroll = React.useCallback(() => {
    if (
      !isActive &&
      window.pageYOffset > containerRef.current?.getBoundingClientRect().top
    ) {
      setActive(true);
    }
  }, [containerRef, isActive]);

  React.useEffect(() => {
    if (containerRef.current) {
      setContainerHeight(containerRef.current.offsetHeight);
    }
  }, [containerRef]);

  React.useEffect(() => {
    window.addEventListener('resize', handleResize);

    return () => window.removeEventListener('resize', handleResize);
  }, [handleResize]);

  React.useEffect(() => {
    window.addEventListener('scroll', handleScroll);

    return () => window.removeEventListener('scroll', handleScroll);
  }, [handleScroll]);

  return (
    <StyledSection ref={containerRef}>
      <TextContainer>
        <SectionTitle>{title}</SectionTitle>
        <SectionDescription>
          Lorem ipsum dolor sit amet, consectetur adipisicing elit. Excepturi
          libero saepe quisquam porro sunt non nisi dolores voluptatum soluta
          aspernatur, nam et quo, corrupti, assumenda quia. Unde aperiam veniam
          illum. Corporis consequuntur molestias voluptates ratione recusandae
          dolores voluptatum illo, mollitia consequatur! Doloremque neque a aut
          doloribus expedita odit magnam assumenda laudantium, consequuntur
          molestias delectus sequi suscipit vitae sunt! Laboriosam quisquam quam
          est voluptatem placeat sunt repellat non nostrum, quidem id. Vitae
          veritatis minus at dolore, dolor minima eaque. Omnis, quasi libero.
          Placeat, ad tempora quae vel distinctio quia ratione quibusdam
          similique doloremque perferendis facere, praesentium voluptatibus hic
          vitae! Cupiditate, consequatur.
        </SectionDescription>
      </TextContainer>
      <SectionNumber active={isActive} containerHeight={containerHeight}>
        1
      </SectionNumber>
      <SectionImage
        alt="Section"
        src={`${process.env.ASSET_PREFIX}/how-to-contribute/workspace.svg`}
      />
    </StyledSection>
  );
};
